<template>
  <div>
    <Header></Header>
    <main class="main dashboard-main">
      <div
        class="container-fluid"
        style="padding-top: 30px; min-height: 100vh"
        v-loading="load"
      >
        <div class="carrinho">
          <i class="fas fa-shopping-cart" @click="drawer2 = true">
            <div class="number" v-if="cart.length >= 1">{{ cart.length }}</div>
          </i>
        </div>
        <el-drawer v-model="drawer2" :direction="direction" size="50%">
          <template #title>
            <h4 style="padding-left: 15px">CARRINHO</h4>
          </template>
          <template #default>
            <div class="carrinho-item" v-loading="load">
              <div class="lista" v-if="cart != false">
                <ul>
                  <li v-for="(item, index) in cart" :key="index" class="row">
                    <div class="col-md-7">
                      <h2>NOME DO CURSO</h2>
                      <p class="nome">{{ item.COURSE_NAME }}</p>
                    </div>
                    <div class="col-md-3">
                      <h2>PREÇO</h2>
                      <p class="preco">{{ currencyBRL(item.COURSE_PRICE) }}</p>
                    </div>
                    <div class="col-md-2">
                      <i
                        class="fas fa-trash-alt excluir"
                        @click="removeCart(item)"
                      ></i>
                    </div>
                  </li>
                </ul>
              </div>
              <div class="lista text-center" v-else>
                <h5>Carrinho Vazio</h5>
              </div>
              <div class="footer row text-center">
                <div class="col-md-12">
                  <h3 style="font-weight: bold; text-transform: uppercase">
                    Valor total
                  </h3>
                  <h5>{{ currencyBRL(total) }}</h5>
                  <br />
                </div>
                <div class="col-md-12">
                  <el-button
                    type="success"
                    round
                    @click="finalizar()"
                    v-if="cart != false"
                    >Finalizar Pedido</el-button
                  >
                </div>
              </div>
            </div>
          </template>
        </el-drawer>
        <br />
        <div class="animated fadeIn">
          <div class="content-header"></div>
          <!--content-header-->
          <div class="row">
            <div class="col">
              <div class="">
                <h4
                  style="margin-left: 20px; font-size: 30px; font-weight: bold"
                >
                  Cursos disponíveis
                </h4>
                <!--card-header-->
                <div class="card-body">
                  <div class="row">
                    <div class="col-md-3" v-for="item in Cursos" :key="item.ID">
                      <div
                        class="best-course-pic-text position-relative border"
                      >
                        <div
                          class="
                            best-course-pic
                            position-relative
                            overflow-hidden
                          "
                          :style="[
                            getURL(item.photo)
                              ? {
                                  background: 'url(' + getURL(item.photo) + ')',
                                }
                              : {
                                  backgroundImage:
                                    'url(' +
                                    require('@/assets/images/1608244179-1.png') +
                                    ')',
                                },
                          ]"
                        ></div>
                        <div class="best-course-text d-inline-block w-100 p-2">
                          <div
                            class="course-title mb20 headline relative-position"
                          >
                            <h5>
                              <a href="#">{{ item.NAME }}</a>
                            </h5>
                          </div>
                          <div class="course-meta d-inline-block w-100">
                            <div class="d-inline-block w-100 0">
                              <p>
                                {{ getDescription(item.DESCRIPTION) }}
                              </p>
                            </div>
                            <div class="preco">
                              <h5>{{ currencyBRL(item.PRICE) }}</h5>
                            </div>
                            <div class="ADD">
                              <el-button
                                type="primary"
                                @click.prevent="addCart(item)"
                              >
                                Adicionar ao carrinho</el-button
                              >
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>
<script>
// @ is an alias to /src
import { reactive, ref } from "vue";
import Header from "../../components/Header/index.vue";
import Footer from "../../components/Footer/index.vue";
import Routes from "../../services/Routes";
import Products from "../../services/Products";

import * as moment from "moment";
import Swal from "sweetalert2";
import AOS from "aos";

export default {
  name: "Home",
  components: {
    Header,
    Footer,
  },
  data() {
    return {
      menu: true,
      Cursos: false,
      drawer2: false,
      cart: false,
      load: true,
      total: 0,
    };
  },
  methods: {
    finalizar() {
      this.load = true;
      let data = {
        USER_ID: JSON.parse(localStorage.getItem("userID")),
        USER_ACCOUNT_ID: JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
          ? JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
          : 0,
      };
      Products.addOrder(data)
        .then((r) => {
          this.$message({
            message: "Ordem Gerada Com Sucesso!",
            customClass: "customADD",
            duration: 1500,
            offset: 0,
            type: "success",
          });
        })
        .catch((e) => {
          this.$message({
            message: "Algo deu errado, tente novamente!",
            duration: 1500,
            type: "error",
          });
          this.load = false;
        })
        .finally(() => {
          this.$router.push("/meus-pedidos");
          this.load = false;
        });
    },
    removeCart(row) {
      this.load = true;
      let data = {
        SHOPPING_CART_ID: row.SHOPPING_CART_ID,
      };
      Products.removeCart(data)
        .then((r) => {
          this.$message({
            message: " Removido do carrinho: " + row.COURSE_NAME + "!",
            customClass: "customADD",
            duration: 1500,
            offset: 0,
            type: "success",
          });
        })
        .catch((e) => {
          this.$message({
            message: "Não removido, tente novamente!",
            duration: 1500,
            type: "error",
          });
          this.load = false;
        })
        .finally(() => {
          this.getCart();
          this.load = false;
        });
    },
    getCart() {
      this.load = true;
      let data = {
        USER_ID: JSON.parse(localStorage.getItem("userID")),
        USER_ACCOUNT_ID: JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
          ? JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
          : 0,
      };
      Products.getCart(data)
        .then((r) => {
          this.cart = r.data.DATA;
          this.total = 0;
          this.cart.map((item) => {
            this.total += Number(item.COURSE_PRICE);
          });
          this.load = false;
        })
        .catch((e) => {
          this.load = false;
        });
    },
    addCart(row) {
      this.load = true;

      let data = {
        USER_ID: JSON.parse(localStorage.getItem("userID")),
        USER_ACCOUNT_ID: JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
          ? JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
          : 0,
        COURSE_ID: row.ID,
        COURSE_PRICE_ID: row.PRICE_ID,
      };

      Products.addCart(data)
        .then((r) => {
          this.$message({
            message: " Adicionado ao carrinho: " + row.NAME + "!",
            customClass: "customADD",
            duration: 1500,
            offset: 200,
            type: "success",
          });
        })
        .catch((e) => {
          this.$message({
            message: "Curso não adicionado, tente novamente!",
            duration: 1500,
            type: "error",
          });
          this.load = false;
        })
        .finally(() => {
          this.getCart();
          this.load = false;
        });
    },
    currencyBRL(value) {
      let val = (value / 1).toFixed(2).replace(".", ",");
      return "R$ " + val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    },
    getPercent(n) {
      let val = (n / 1).toFixed(2).replace(".", ",");
      return "R$ " + val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    },
    getDescription: function (name) {
      if (name.length > 150) {
        return name.substring(0, 150) + "...";
      }
      return name;
    },
    getURL(item) {
      var a = "";
      for (var obj in item) {
        a = item[obj];
        if (a.includes("https", 0) == true) {
          return a;
        } else {
          return null;
        }
      }
    },
    goTO(url) {
      this.$router.push(url);
    },
    getCourses() {
      this.load = true;
      let USER_ACCOUNT_ID = JSON.parse(
        localStorage.getItem("SELECTED_ACCOUNT_ID")
      )
        ? JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
        : 0;
      let USER_ID = JSON.parse(localStorage.getItem("userID"));
      Products.SearchProducts(USER_ID, USER_ACCOUNT_ID)
        .then((r) => {
          this.Cursos = r.data.DATA;
        })
        .catch((e) => {
          console.log(e);
          this.load = false;
        });
    },
  },
  mounted() {
    this.getCourses();
    this.getCart();
    AOS.init();
  },

  created() {},
  setup() {},
};
</script>

<style scoped>
.app-header .navbar-toggler {
  display: none;
}

.best-course-pic-text .best-course-pic {
  width: 100% !important;
  height: 250px !important;
}

.best-course-pic-text {
  border-radius: 4px;
  padding-top: 0px;
}

.best-course-pic-text {
  cursor: pointer;
}

.app-header .navbar-brand {
  width: 150px;
  padding: 5px 10px;
}
.concluido {
  width: 100%;
  background-color: #017803;
}
.best-course-pic-text {
  cursor: default;
}
</style>

<style>
.ADD .el-button {
  font-weight: bold;
  color: white;
  width: 100%;
}
.best-course-pic-text .best-course-text .course-meta span {
  color: #ffffff;
  width: 100%;
  font-weight: bold;
}
.preco h5 {
  font-size: 25px;
  margin-bottom: 15px;
}
.carrinho {
  position: fixed;
  right: 65px;
  top: 85px;
  cursor: pointer;
  z-index: 9;
}
.carrinho i {
  color: #409eff;
  border: 2px solid #409eff;
  padding: 14px 10px;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  font-size: 20px;
  background: white;
  cursor: pointer;
  position: relative;
}
.carrinho .number {
  position: absolute;
  background: #409eff;
  color: white;
  font-weight: bold;
  font-family: sans-serif;
  padding: 3px;
  border-radius: 10px;
  right: -12px;
  top: 0;
  bottom: 0;
  height: max-content;
  margin: auto;
}
.carrinho-item {
  padding: 1vw 2vw;
}

.carrinho-item .top {
  padding: 1vw 2vw;
}

.carrinho-item .lista {
  height: 63vh;
  overflow-y: scroll;
  overflow-x: hidden;
  padding-right: 30px;
}
.carrinho-item .lista ul {
  display: block;
  list-style-type: decimal;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0;
}
.carrinho-item .lista li {
  background: #fbfbfb;
  margin-bottom: 5px;
  border-bottom: 2px solid #d7d7d7;
  padding: 5px 8px;
}
.carrinho-item .lista h2 {
  font-size: 18px;
  color: #72767b;
  margin: 8px 0px 5px 0px;
}
.carrinho-item .lista .nome {
  color: #4b92db;
  font-weight: 500;
}
.carrinho-item .lista .preco {
  color: #0b6412;
  font-weight: 500;
}
.carrinho-item .lista .excluir {
  color: #760404;
  font-weight: 500;
  padding-top: 30px;
  cursor: pointer;
}
.carrinho-item .lista li:hover {
  background: #ededed;
  border-bottom: 2px solid #68a3e0;
}
</style>
